*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.game-area{
	position: relative;
	margin: 0 auto 50px;
	width: 600px;
	height: 600px;
	border: 2px solid;
}

.snake-dot{
	position: absolute;
	width: 2%;
	height: 2%;
	background: #228B22;
	border: 1px solid white;
	z-index: 2;
}


.snake-food{
	position: absolute;
	width: 2%;
	height: 2%;
	background: red;
	border: 1px solid white;
	z-index: 1;
}
.score{
	padding-top: 50px;
	text-align: center;
}
